import React from 'react';
import { Box, useMediaQuery, Link, Card, Button } from '@mui/material';
import Girl from '../src/res/thumbs_up_girl.png';


export default function MathPage() {
  return (
    <Box sx={{display: 'flex', gap: 5, flexDirection: 'column'}}>
      <Card elevation={3} sx={{display: 'flex', flexDirection: 'row', alignItems: 'center', justifyContent: 'space-around', backgroundColor: '#e49278'}}>
      <Box sx={{padding: 5}}>
        <h1>Matematyka</h1>
        <h2>Zakres nauczania:</h2>
        <ul>
          <li>szkoa podstawowa</li>
          <li>szkoa rdenia na poziomie podstawowym</li>
          <li>szkoa rednia na poziomie rozszerzonym</li>
        </ul>
      </Box>
      <img
        src={Girl}
        alt="Logo"
        style={{ height: 300, display: { xs: 'none', md: 'flex'}, marginRight: '8px' }}
      />
      </Card>
      <Box sx={{textAlign:'center'}}>
        <p>Skontaktuj si z nami i razem zrealizujmy twj cel w nauce tego przedmiotu! Zwiksz pewno siebie, pracuj z dowiadczonymi korepetytorami i czerp przyjemno z zaj online, ju teraz umw si na darmow lekcj prbn z przedmiotu: Matematyka</p>
        <Button>
          Darmowa lekcja prbna
        </Button>
      </Box>

    </Box>
  );
}
